section.compensation {
	.fancybox-button.fancybox-button--close {
		background: map-get($mau, vang) !important;
		color: white !important;
	}

	position: relative;

	.compensation__wrapper {
		margin: 0 auto;
		width: 100%;
		height: 832px;

		@media (max-width: 575.98px) {
			height: 575px;
		}

		@media (max-width: 1024.98px) {
			display: block;
			width: 100%;
		}
	}

	.compensation__img {
		position: absolute;
		top: 0;
		left: 0;
		height: 832px;

		@media (max-width: 575.98px) {
			height: 575px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.compensation__content {
		display: block;
		position: relative;
		width: calc(100% - 110px);
		padding: 130px 0;

		@media (max-width: 1024.98px) {
			width: 100%;
			margin: 0 auto;
			padding: 75px 0;
		}

		@media (max-width: 575.98px) {
			padding-top: 25px;
		}
	}

	.compensation__contentBox {
		width: 735px;
		margin-left: auto;

		@media (max-width: 1439.98px) {
			width: 650px;
		}

		@media (max-width: 1024.98px) {
			width: 100%;
		}
	}

	.compensation__contentBox--title {
		h3 {
			font-size: 30px;
			font-family: Averta-Bold;
			color: map-get($mau, vang);
			text-transform: uppercase;
			margin-bottom: 20px;

			@media (max-width: 1439.98px) {
				font-size: 25px;
			}

			@media (max-width: 1024.98px) {
				font-size: 30px;
			}

			@media (max-width: 574.98px) {
				font-size: 18px;
			}
		}
	}

	.compensation__contentBox--desc {
		padding: 5px;
		margin-bottom: 20px;
		overflow-y: auto;
		max-height: 270px;

		p {
			font-size: 1rem;
			font-family: Averta-Light;
			color: map-get($mau, xam);

			+p {
				margin-top: 5px;
			}
		}

		@media (max-width: 1719.98px) {
			background: #f1f0f0;
			border-radius: 10px;
			padding: 25px;
		}

		@media (max-width: 575.98px) {
			padding: 15px;
		}
	}

	.compensation__contentBox--list {
		width: 100%;
		background: #F1F0F0;
		border-radius: 10px;
		box-shadow: 0 3px 6px #0000001f;
		padding: 45px 70px;
		max-height: 361px;
		overflow-y: auto;

		@media (max-width: 1024.98px) {
			padding: 25px;
		}

		@media (max-width: 575.98px) {
			padding: 15px;
			max-height: 210px;
		}
	}

	.compensation__contentBox--item {
		position: relative;

		&:first-child {
			padding-top: 0px !important;
			margin-top: 0px !important;
		}

		&:nth-child(n+1) {
			padding-top: 40px;
			margin-top: 40px;

			@media (max-width: 1024.98px) {
				padding-top: 20px;
				margin-top: 20px;
			}

			@media (max-width: 575.98px) {
				padding-top: 10px;
				margin-top: 10px;
			}
		}

		h4 {
			font-family: Averta-Bold;
			font-size: 1.5625rem;
			text-transform: uppercase;
			color: map-get($mau, xam);

			@media (max-width: 575.98px) {
				font-size: 1rem;
				padding-right: 0;
			}
		}

		.compensation__item-pdf {
			cursor: pointer;
			display: flex;
			align-items: center;
			padding-top: 7px;

			span {
				position: relative;
				font-size: 18px;
				padding-left: 15px;
				color: map-get($mau, xam);
				font-family: Averta-Light;
				padding-right: 20px;

				@media (max-width: 575.98px) {
					font-size: 15px;
				}

				&::before {
					position: absolute;
					content: "";
					background-image: url('../assets/icons/arrow-double.png');
					background-repeat: no-repeat;
					top: 59%;
					transform: translateY(-50%);
					right: 0;
					width: 13px;
					height: 13px;
					background-size: 100% 100%;
				}
			}
		}
	}

	.compensation__contentBox--item+.compensation__contentBox--item {
		&::before {
			position: absolute;
			height: 1px;
			width: 100%;
			content: "";
			top: 0;
			right: 0;
			background: map-get($mau, xam);
		}

		&::after {
			position: absolute;
			height: 11px;
			width: 11px;
			border-radius: 50%;
			content: "";
			top: -6px;
			right: 0;
			background: map-get($mau, xam);
		}
	}
}