footer {
	position: relative;
	background-image: url(../assets/bg-footer.png);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;

	// padding-top: 13px;
	@media (min-width: 1440px) {
		background-size: 100% 100%;
	}

	// &::before {
	//     content: '';
	//     position: absolute;
	//     width: 100%;
	//     height: 13px;
	//     background: #ffffff;
	//     top: 0;
	//     left: 0;
	//     box-shadow: 0 3px 6px rgba($color: #000000, $alpha: .16);
	//     width: 100%;
	// }
}

.footer__mainWrapper {
	height: 100%;
	padding: 50px 0;

	@media (min-width: 1440px) {
		width: calc(100% - 220px);
		margin: 0 auto;
	}

	@media (max-width: 575.98px) {
		padding: 25px 0;
	}
}

.footer--title {
	font-family: Averta-Bold;
	font-size: 1.25rem;
	padding-bottom: 10px;
	margin-bottom: 15px;
	border-bottom: 1px solid rgba($color: #777171, $alpha: .3);
}

.footer--siteMaps {
	display: flex;
	padding-left: 0;
	list-style-type: none;

	ul {
		padding-left: 0;
		list-style-type: none;

		li+li {
			margin-top: 5px;
		}
	}

	.footer__site-maps--link {
		font-size: 1rem;
		color: #777171;
		font-family: Averta-Bold;

		&.active {
			color: map-get($mau, xanh-2);
		}
	}

	@media (max-width: 1024.98px) {
		display: block;

		.item__footer-siteMaps {
			margin-bottom: 15px;
		}
	}
}

.item__footer-siteMaps {
	+.item__footer-siteMaps {
		padding-left: 60px;

		@media (max-width: 1199.98px) {
			padding-left: 15px;
		}

		@media (max-width: 1024.98px) {
			padding-left: 0;
		}
	}
}

.footer--contact {
	ul {
		padding-left: 0;
		list-style-type: none;
	}

	.footerContact--item {
		display: flex;
		margin-bottom: 10px;
	}

	.footer-contact--icon {
		-ms-flex: 0 0 100%;
		-webkit-box-flex: 0;
		flex: 0 0 30px;
		max-width: 30px;
		padding-right: 10px;
	}

	.footerContact--link {
		-ms-flex-preferred-size: 0;
		flex-basis: 0;
		-ms-flex-positive: 1;
		flex-grow: 1;
		max-width: 100%;
		font-family: Averta-Light;
		font-size: 1rem;
		color: #777171;

		@media (max-width: 1440.98px) {
			font-size: 15px;
		}
	}
}

.coppy-right {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 42px;
	padding: 0 10px;

	p {
		font-size: 0.875rem;
	}
}